/** @jsx jsx */
import { jsx } from 'theme-ui'
import { motion } from 'framer-motion'
import { useInView } from 'react-intersection-observer'

import DesignFeature from './DesignFeature'
import DevFeature from './DevFeature'
import ServiceCell from './ServiceCell'
import QuoteCarousel from '../QuoteCarousel'
import Header from '../common/Header'

import {
  ServiceDevIcon,
  ServiceDesignIcon,
  ServiceProductIcon,
  ServiceStrategyIcon,
  ServiceTeamIcon,
  CheckCircleIcon,
  ProductStrategyIllustration,
} from '../Icons'
import { useEffect, useState } from 'react';
import { animateOpacity } from '../../styles/animations';

export const IDS_FOR_SCROLL = {
  dev: 'dev',
  design: 'design',
  strategy: 'strategy',
  teamEnhancement: 'team-enhancement',
  serviceProduct: 'service-product'
}

const serviceData = [
  {
    Component: DevFeature,
    topic: 'Build',
    title: 'Web and mobile development',
    showComponentOnMobile: true,
    description:
      'From MVPs to scalable enterprise software, Bitmatica uses cutting-edge web technology to ship features with your team and build modern architecture for future growth.',
    services: [
      'Frontend (React, GraphQL)',
      'Backend (Node, Ruby, Scala)',
      'iOS (Swift / Objective-C)',
      'Android (Kotlin / Java)',
      'React Native',
    ],
    id: IDS_FOR_SCROLL.dev
  },
  {
    Component: DesignFeature,
    topic: 'Design',
    title: 'Product Design (UI & UX)',
    showComponentOnMobile: false,
    description:
      'Beautiful products require beautiful design, and we team up with you and your customers to deliver a thoughtful and stunning product.',
    services: ['User Experience Design', 'Visual Design', 'Interaction Design', 'High Fidelity Prototypes'],
    id: IDS_FOR_SCROLL.design
  },
  {
    Component: ProductStrategyIllustration,
    topic: 'Ship',
    title: 'Product Strategy',
    showComponentOnMobile: false,
    description:
      'We are on a mission to build the right product, not just what our clients ask of us. Every relationship starts with a deep understanding of your business, customers, stakeholders, and unique constraints, so together, we can be successful.',
    services: ['Product Definition', 'Customer Validation', 'Market Research', 'Competitive Analysis'],
    id: IDS_FOR_SCROLL.strategy
  },
]

const ServiceBlock = ({ Component, topic, title, description, services, id, showComponentOnMobile }) => {
  const [ref, inView] = useInView({
    threshold: 0.2,
  })

  const content = (
    <div>
      <div sx={{ mb: '1rem', fontWeight: 600, color: 'purple' }}>
        {topic}
      </div>
      <h2 sx={{ mb: '1rem' }}>{title}</h2>
      <div sx={{ mb: '1rem', color: 'text' }}>{description}</div>
      {services.map(service => (
        <div key={service} sx={{ display: 'flex', alignItems: 'center', mb: '.5rem' }}>
          <CheckCircleIcon fill={'#755BFF'} />
          <div sx={{ ml: '.75rem' }}>{service}</div>
        </div>
      ))}
    </div>
  )
  return (
    <div
      ref={ref}
      id={id}
      sx={{
        display: 'flex',
        flexDirection: ['column', , 'row'],
        py: ['4rem', '4rem', '8rem'],
        alignItems: 'center',
      }}
    >
      <noscript>
        <div sx={{ mb: '1rem', fontWeight: 600, color: 'purple' }}>
          {topic}
        </div>
      </noscript>
      <div
        className={REMOVE_IF_JS_CLASSNAME}
        sx={{
          width: ['100%', , '50%'],
          display: [showComponentOnMobile ? 'flex' : 'none', , 'flex'],
          alignItems: 'center',
          justifyContent: 'center',
        }}
        css={css`
          &.${REMOVE_IF_JS_CLASSNAME} {
            display: none !important;
          }
        `}
      >
        <Component inView={inView} styles={{ mb: ['2rem', '2rem', 0] }} />
      </div>
      <motion.div
        initial={{ opacity: 0, y: 50 }}
        animate={inView && { opacity: 1, y: 0 }}
        transition={{ delay: .3, ease: 'easeOut' }}
        sx={{ width: ['100%', , '50%'] }}
      >
        {content}
      </motion.div>
      <noscript>
        {content}
      </noscript>
    </div>
  )
}

const variants = {
  visible: {
    transition: {
      staggerChildren: 0.07,
    },
  },
  hidden: {
    transition: {
      staggerChildren: 0.05,
    },
  },
}



const ServicesContent = () => (
  <div>
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      sx={{ mb: '1rem', fontWeight: 600 }}
      css={css`
        animate: ${animateOpacity};
      `}
    >
      What we do
    </motion.div>
    <noscript sx={{ mb: '1rem', fontWeight: 600 }}>What we do</noscript>
    <ServiceCell
      id={IDS_FOR_SCROLL.dev}
      icon={<ServiceDevIcon />}
      title={'Web and Mobile Development'}
      styles={{ mb: '.5rem' }}
    />
    <ServiceCell
      id={IDS_FOR_SCROLL.design}
      icon={<ServiceDesignIcon />}
      title={'Product Design (UI & UX)'}
      styles={{ mb: '.5rem' }}
    />
    <ServiceCell
      id={IDS_FOR_SCROLL.strategy}
      icon={<ServiceStrategyIcon />}
      title={'Product Strategy'}
    />

    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      sx={{ mt: '2rem', mb: '1rem', fontWeight: 600 }}
    >
      How we fit in
    </motion.div>
    <noscript sx={{ mt: '2rem', mb: '1rem', fontWeight: 600 }}>How we fit in</noscript>
    <ServiceCell
      id={IDS_FOR_SCROLL.teamEnhancement}
      icon={<ServiceTeamIcon />}
      title={'Team Enhancement'}
      styles={{ mb: '.5rem' }}
    />
    <ServiceCell
      id={IDS_FOR_SCROLL.serviceProduct}
      icon={<ServiceProductIcon />}
      title={'Concept to Reality'}
    />
  </div>
)

const Services = () => {
  const [ref, inView] = useInView({
    rootMargin: '-100px 0px',
  })

  const [jsAvailable, setJsAvailable] = useState()

  useEffect(() => {
    setJsAvailable(true)
  })

  return (
    <div>
      <div ref={ref}>
        <div
          sx={{
            display: 'flex',
            mb: '8rem',
            flexDirection: ['column', 'column', 'row'],
          }}
        >
          <div sx={{ width: ['100%', , '50%'], px: [0, , '2rem'], mb: ["4rem", , "0rem"] }}>
            {jsAvailable ?
              <motion.div
                initial={'hidden'}
                animate={inView && 'visible'}
                variants={variants}
                transition={{ delay: 0.1 }}
              >
                <ServicesContent />
              </motion.div> :
              <ServicesContent />
            }
          </div>

          <div sx={{ width: ['100%', , '50%'], px: [0, , '2rem'] }}>
            <motion.div
              sx={{ mb: '1rem', fontWeight: 600 }}
              transition={{ delay: 0.1 }}
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
            >
              What our clients say
            </motion.div>
            <noscript sx={{ mb: '1rem', fontWeight: 600 }}>What our clients say</noscript>
            <QuoteCarousel inView={inView} />
          </div>
        </div>
        <Header>
          The expertise you need to ship <br /> high quality software
        </Header>
        {serviceData.map(service => (
          <ServiceBlock
            key={service.title}
            Component={service.Component}
            topic={service.topic}
            title={service.title}
            description={service.description}
            services={service.services}
            id={service.id}
            showComponentOnMobile={service.showComponentOnMobile}
          />
        ))}
      </div>
    </div>
  )
}

export default Services
