import { REMOVE_IF_JS_CLASSNAME } from "../html";

export default function removeClassname() {
  const els = document.getElementsByClassName(REMOVE_IF_JS_CLASSNAME)
  console.log('els')
  console.log(els.length)
  typeof document !== 'undefined' && Array.from(els).forEach(function(el) {
    console.log('1')
    console.log(el)
    el.classList.remove(REMOVE_IF_JS_CLASSNAME)
    console.log('2')
    console.log(el)
  })
}
